.pp-search-input-sm {
  border: 1px solid #e0e0e0;
  color: #e0e0e0;
  background-color: white;
  padding: 0px;
  border-radius: 2px;
  min-height: 0;
  font-size: 12px;
}

.pp-selectable-color-red:hover,
.pp-selectable-color-negative:hover {
  color: var(--q-color-negative) !important;
}

.pjm-selector {
  font-size: 13px;
  border-radius: 2px;
  min-height: 24px;
  line-height: 24px;
}

.pp-search-button {
  min-height: 0;
  padding: 6px 16px;
}

.pp-input-focus:hover {
  border: 1px solid #9e9e9e;
  border-radius: 2px;
}

.pp-border-red {
  border: 1px solid var(--q-color-negative);
}

.no-ripple .q-radial-ripple:after, .no-ripple .q-ripple-animation {
  -webkit-transition: none;
  transition: none;
  width: 0 !important;
  height: 0 !important;
}

.pp-login-bg {
  position: fixed;
  height: 100%;
  width: 100%;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-position: center;
  background-size: cover;
  background: url(/static/img/login_bg.fb07b0e.jpg);
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

.pp-login-wrap {
  position: absolute;
  width: 310px;
  height: 420px;
  padding: 20px 25px;
  z-index: 3;
  background: rgba(216, 220, 229, 0.5);
}

.pp-login-bg .q-input .q-icon {
  margin-top: 7px;
}
